function sliderDelivery() {
    var swiper = new Swiper('.delivery .swiper', {
        slidesPerView: 4,
        spaceBetween: 13,
        pagination: {
            el: ".delivery .swiper-pagination",
            type: "progressbar",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            371: {
                slidesPerView: 1.5,
            },
            576: {
                slidesPerView: 2,
            },
            993: {
                slidesPerView: 3,
            },
            1201: {
                slidesPerView: 4,
            },
        }
    })
}

