function Sliderproduct() {
    var swiper = new Swiper('.product .swiper', {
        slidesPerView: 1,
        spaceBetween: 10,
        navigation: {
            nextEl: '.product .swiper-button-next',
            prevEl: '.product .swiper-button-prev',
        },
        pagination: {
            el: ".product .swiper-pagination",
            clickable: true,
        },
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        slidesPerView: 4,
        spaceBetween: 15,
        freeMode: true,
        watchSlidesProgress: true,
        direction: "horizontal",
        breakpoints: {
            320: {
                slidesPerView: 2,
            },
            575: {
                slidesPerView: 4,
            },
        }
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 10,
        slidesPerView: 1,
        thumbs: {
            swiper: swiper,
        }
    });
}

let SlidersArray = []

function sliderProducts() {
    $(".products").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.products[data-slider-id="${index}"] .swiper`, {
            slidesPerView: 4,
            spaceBetween: 15,
            observer: true,
            observeParents: true,
            navigation: {
                nextEl: `.products[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.products[data-slider-id="${index}"] .swiper-button-prev`,
            },
            breakpoints: {
                320: {
                    slidesPerView: "auto",
                    spaceBetween: 12,
                },
                768: {
                    slidesPerView: 2.7,
                    spaceBetween: 24,
                },
                993: {
                    slidesPerView: 3.5,
                    spaceBetween: 24,
                },
                1201: {
                    slidesPerView: 4,
                    spaceBetween: 15,
                },
            }
        })
    })
}



$(document).ready(function() {

  sliderDelivery();
  sliderRecipes();
  SliderPopularRecipes();
  SliderVideoRecipes();
  SliderProductCatalog();
  Sliderproduct();
  gallery();
  sliderProducts()

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    // Tabs
    $(".making__tab2").click(function () {
        let path = $(this).attr("data-tab-path2")
        $(this).siblings(".making__tab2").removeClass("making__tab2--active")
        $(this).addClass("making__tab2--active")

        $(this).parents(".making__inner").find(".making__content").removeClass("making__content--active")
        $(this).parents(".making__inner").find(`.making__content[data-content-path2="${path}"]`).addClass("making__content--active")
    })

    // Video
    $('.play').click(function () {
        $(this).addClass('play--active')
        $(this).siblings('.video')[0].play();
        $(this).siblings('.video-recipe__card-title').addClass('video-recipe__card-title--active')
        $(this).siblings('.video-recipe__card-btns').addClass('video-recipe__card-btns--active')
    })

    $('.video').click(function () {
        $(this).siblings('.play').toggleClass('play--active')
        $(this).siblings('.video-recipe__card-btns').toggleClass('video-recipe__card-btns--active')
        $(this).siblings('.video-recipe__card-title').addClass('video-recipe__card-btns--active')
    })

    // Select
    $(".custom_select select").niceSelect()

    // Accordion
    $(".categories__item-show").click((function(){
    $(this).toggleClass("categories__item-show--active"),
    $(this).siblings(".categories__item-hidden").slideToggle()}));

    // Settings
    $('.categories__settings').click(function () {
        $('.categories__sidebar').addClass('categories__sidebar--active')
        $('body').addClass('fixed-body')
    })

    $('.categories__sidebar-back').click(function () {
        $('.categories__sidebar').removeClass('categories__sidebar--active')
        $('body').removeClass('fixed-body')
    })

    // Hover - info
    $('.weight-info').hover(function () {
        $(this).children('.hover-hide').toggleClass('hover-hide--active')
    })

    //
    $('.about-product__btn').click(function () {
        $(this).toggleClass('about-product__btn--active')
        $('.about-product__list').toggleClass('about-product__list--active')
    })

    $('.product-structure__btn').click(function () {
        $(this).toggleClass('product-structure__btn--active')
        $('.product-structure__right').toggleClass('product-structure__right--active')
        $('.product-structure__bottom').toggleClass('product-structure__bottom--active')
    })

    // Range
    let rangeBlocks = document.querySelectorAll("._range-block")

    rangeBlocks.forEach(rangeBlock => {
        let rangeSlider = rangeBlock.querySelector(".range-slider")
        let rangeOutput = rangeBlock.querySelector(".basket__range-number")
        let min = +rangeBlock.getAttribute("data-min")
        let max = +rangeBlock.getAttribute("data-max")
        console.log(min)
        console.log(max)

        if (rangeSlider) {
            noUiSlider.create(rangeSlider, {
                start: 0,
                connect: [true, false],
                step: 1,
                range: {
                    'min': min,
                    'max': max
                }
            });

            rangeSlider.noUiSlider.on('update', function (values, handle) {
                rangeOutput.innerHTML = Math.floor(values[handle]);
            });
        }
    })

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

    // Mask-tel
    $(".form-tel").mask("+7 (999)-999-99-99");

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

    // Header
    $(".collapse").hover(onIn, onOut);
    $(".collapse .collapse__btn").click(function() {
        if ($(this).parent(".collapse").hasClass("collapse--active")) {
            $(this).parent(".collapse").removeClass("collapse--active")
        } else {
            $(this).parent(".collapse").addClass("collapse--active")
        }
    })

    // Circle
    $('.circle15').circleProgress({
        value: 0.15,
        startAngle: 11,
        thickness: 5,
        fill: {
            gradient: ["#EE842C"]
        }
    });

    $('.circle25').circleProgress({
        value: 0.25,
        startAngle: 11,
        thickness: 5,
        fill: {
            gradient: ["#EE842C"]
        }
    });

    $('.circle0').circleProgress({
        value: 0.0,
        startAngle: 11,
        thickness: 5,
        fill: {
            gradient: ["#EE842C"]
        }
    });

    var circles = $('.circle0, .circle15, .circle25');

    circles.appear({ force_process: true });

    circles.on('appear', function() {
        var circle = $(this);
        if (!circle.data('inited')) {
            circle.circleProgress({
                value: $(this).data('value'),
            });
            circle.data('inited', true);
        }
    });

    // Scroll
    window.addEventListener('scroll', function(e) {
        if (document.documentElement.clientWidth > 1299 && window.scrollY > 0) {
            document.body.style.paddingTop = pageHeader.clientHeight + 'px';
            pageHeader.classList.add('fixed')
        }

        else if (document.documentElement.clientWidth < 1300 && window.scrollY > topLine.clientHeight) {
            let bodyTopPadding = pageHeader.clientHeight

            document.body.style.paddingTop = bodyTopPadding + 'px';

            pageHeader.classList.add('fixed')
        }

        else {
            document.body.style.paddingTop = ''
            pageHeader.classList.remove('fixed')
        }
    })

    // Accordion
    $(".menu-categories-all").click((function() {
        if (window.matchMedia("(max-width: 1299px)").matches) {
            $(".menu-categories__hide").toggleClass('menu-categories__hide--active')
        }
    }))

    // Favorite
    $('.favorite').click(function () {
        $(this).toggleClass('favorite--active')
    })

    // Footer Accordion
    $('.footer-show').click(function () {
        if (window.matchMedia("(max-width: 767px)").matches) {
            $(this).toggleClass("footer-show--active"),
            $(this).siblings(".footer-hidden").slideToggle();
        }
    })

})

function onIn() {
    if (window.innerWidth > 1299) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("collapse--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 1299) {
        $(this).removeClass("collapse--active")
    }
}

// Index Scripts
'use strict'

let intPattern = /^\d{1,}$/;
const inputsInt = document.querySelectorAll('.plus-minus input')
const pageHeader = document.querySelector('.page-header');
const topLine = document.querySelector('.top-line');
const mobileLayerMenu = document.querySelector('#mobile-layer-menu');
const menuCatalogCategoriesItems = document.querySelectorAll('.menu-catalog__categories .collapse__btn');
let productsInOrder = 0;
let productsInOrderShowing = document.querySelector('.order-number');

document.addEventListener('click', function(e) {

    if(e.target.closest('.dropdown-order-item__close')) {
        e.target.closest('.dropdown-order-item').remove()
    }

    if (e.target.closest('.menu-categories-all') && document.documentElement.clientWidth < 1300) {
        e.preventDefault();
    }


    if(e.target.closest('[data-modal-layer]')) {
        let btnModalLayer = e.target.closest('[data-modal-layer]');

        if (document.querySelector('.mobile-layer.shown')) {
            if (btnModalLayer.classList.contains('is-active')) {
                hideMobileLayer()
            }

            else {
                hideMobileLayer()
                showMobileLayer(e)
            }
        }

        else {
            showMobileLayer(e)
        }
    }

    if (e.target.closest('.mobile-layer .modal-back')) {
        hideMobileLayer()
    }


    if (e.target.closest('.fixed-icons button')) {
        window.scrollTo(0, 0);
    }
})


let headerSlider = document.querySelector('.page-header__slider .swiper');
if (headerSlider) {
    new Swiper(headerSlider, {
        slidesPerView: 'auto',
        speed: 400,
        spaceBetween: 32,
        navigation: {
            nextEl: ".page-header__slider .swiper-button-next",
            prevEl: ".page-header__slider .swiper-button-prev",
        },
        breakpoints: {
            0: {
                spaceBetween: 10,
            }
        },
    });
}


const mainBanner = document.querySelector('.swiper-main-banner');
if (mainBanner) {
    new Swiper(mainBanner, {
        slidesPerView: 1,
        loop: true,
        pagination: {
            el: ".main-banner-pagination",
            type: "bullets",
            clickable: true,
        },
        navigation: {
            nextEl: ".swiper-main-banner .swiper-button-next",
            prevEl: ".swiper-main-banner .swiper-button-prev",
        },
    });
}


const mainComments = document.querySelector('.swiper-comments');
if (mainComments) {
    new Swiper(mainComments, {
        slidesPerView: 1,
        loop: true,
        pagination: {
            el: ".swiper-comments-pagination",
            type: "progressbar",
            clickable: true,
        },
    });
}


let productOfDay = document.querySelector('.swiper-product-of-day');
if (productOfDay) {
    new Swiper(productOfDay, {
        loop: true,
        speed: 400,
        navigation: {
            nextEl: ".product-of-day-wrapper .swiper-button-next",
            prevEl: ".product-of-day-wrapper .swiper-button-prev",
        },
        breakpoints: {
            0: {
                slidesPerView: 'auto',
            },
            992: {
                slidesPerView: 2,
            }
        },
    });
}

document.querySelectorAll('.offer').forEach(el => {
    el.addEventListener('click', function(e) {
        if (e.target.closest('.plus-minus button')) {
            e.preventDefault();
        }
    })
});


inputsInt.forEach(el => {
    el.addEventListener('keyup', function() {
        while (el.value.length > 0) {
            if (!intPattern.test(el.value)) {
                el.value = el.value.slice(0, -1);
            }

            else {
                break
            }
        }
    })
});

document.querySelectorAll('.plus-minus button:first-of-type').forEach(el => {
    el.addEventListener('click',function(e) {
        let input = e.target.closest('.plus-minus').querySelector('input')
        if (+input.value > 1) {
            input.value = +input.value - 1
        }

        else {
            input.value = 0;
            e.target.closest('.plus-minus').classList.add('plus-minus--hidden')
        }
        productsInOrder--

        showProductsInOrder()
    })
});

document.querySelectorAll('.plus-minus button:last-of-type').forEach(el => {
    el.addEventListener('click',function(e) {
        let input = e.target.closest('.plus-minus').querySelector('input')
        input.value = +input.value + 1
        e.target.closest('.plus-minus').classList.remove('plus-minus--hidden')

        productsInOrder++;

        showProductsInOrder()
    })
});

function showProductsInOrder() {
    if (productsInOrder > 0) {
        productsInOrderShowing.classList.add('order-number--shown')
        productsInOrderShowing.querySelector('mark').innerText = productsInOrder
    }

    else {
        productsInOrderShowing.classList.remove('order-number--shown')
    }
}


document.querySelectorAll('.insert-from').forEach(el => {
    el.addEventListener('click', function(e) {
        let selectedItem = e.target.closest('[data-value-to-insert]');

        if(selectedItem) {
            let insertTo = e.target.closest('.dropdown-hover').querySelector('.insert-to');
            let insetFrom = e.target.closest('.dropdown-hover').querySelector('.insert-from');

            if (insetFrom.querySelector('.selected')) {
                insetFrom.querySelector('.selected').classList.remove('selected')
            }

            selectedItem.classList.add('selected')
            insertTo.innerText = e.target.closest('[data-value-to-insert]').dataset.valueToInsert
            insertTo.closest('.dropdown-hover .dropdown-hover__btn').classList.add('inserted')
        }
    })
});


