window.addEventListener('resize', function() {
    if (document.documentElement.clientWidth > 1299) {
        hideMobileLayer()
    }
})


const btnUp = document.querySelector('.fixed-icons button');

window.addEventListener('scroll', function() {
    if (window.scrollY > 200) {
        btnUp.classList.add('shown')
    }

    else {
        btnUp.classList.remove('shown')
    }
})


function hideMobileLayer() {
    let mobileLayerShown = document.querySelector('.mobile-layer.shown');

    mobileLayerShown.setAttribute('style', '');
    mobileLayerShown.classList.remove('shown')
    document.querySelector('[data-modal-layer].is-active').classList.remove('is-active')

    scrollLock.enablePageScroll();
}


function showMobileLayer(e) {
    let mobileLayer = document.querySelector(`#${e.target.closest('[data-modal-layer]').dataset.modalLayer}`);
    let paddingTopMobileLayer;


    mobileLayer.classList.add('shown');
    e.target.closest('[data-modal-layer]').classList.add('is-active')

    if (window.scrollY > 0) {
        paddingTopMobileLayer = pageHeader.clientHeight - 35;
    }

    else {
        paddingTopMobileLayer = pageHeader.clientHeight + topLine.clientHeight - 38;
    }

    mobileLayer.style.setProperty('padding-top', `${paddingTopMobileLayer}px`);

    scrollLock.disablePageScroll(mobileLayer.querySelector('.scrollable') ? mobileLayer.querySelector('.scrollable') : mobileLayer);
}




